.container {
    display: flex;
    margin-top: 48px;
}

.contacts {
    color: #FFFFFF;
    margin-right: 50px;
    background-color: #121212;
    padding: 35px 35px;
}


.contact {
    display: flex;
    margin-bottom: 32px;
    margin-top: 8px;
    align-items: center;
    color: #7B7B7B;
}



.contact span {
    margin-left: 10px;
}



.contactsForm {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.contactsForm h3 {
    color: #FFFFFF;
    margin-bottom: 24px;
}

.contactsForm p {
    color: #7B7B7B;
    margin-bottom: 48px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.02em;
}


.input {

    border: none;
    border-bottom: 2px solid #FFFFFF;
    background-color: transparent;

    color: #FFFFFF;
    outline: none;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.02em;
    margin-bottom: 24px;
}


.input_textarea {
    resize: none;

}

.contactsForm button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 56px;
    margin-top: 48px;
    background-color: #C4E300;
    border: none;

}

.contactsForm button a {
    color: #121212;
    text-decoration: none;
}

@media screen and (max-width: 769px) {
    .pages_routs {
        width: 100%;
        left: 0 !important;
    }


}

@media screen and (max-width: 576px) {
    .container {
        display: flex;
        margin-top: 0;
        flex-direction: column;
        margin-bottom: 25px;
    }
    .contacts {
        color: #FFFFFF;
        margin-right: 0;
        background-color: #121212;
        padding: 35px 35px;
        margin-bottom: 25px;
    }

}
